.landingContainer {
    position: relative;
    width: 100vw;
    height: 100vh;
    background-color: var(--color-background);
    overflow: visible;
}

/* Sphere Positioning - containers large enough for expansion with camera zoom */
.sphereTopLeft,
.sphereTopRight,
.sphereBottomLeft {
    position: absolute;
    width: 700px;
    height: 700px;
    min-width: 0;
    min-height: 0;
    overflow: visible !important;
    pointer-events: none;
    z-index: 1;
}

/* Z-index ordering - bottom sphere should be on top for click priority */
.sphereBottomLeft {
    z-index: 3;
}

.sphereTopRight {
    z-index: 2;
}

.sphereTopLeft {
    z-index: 1;
}

.sphereTopLeft > *,
.sphereTopRight > *,
.sphereBottomLeft > * {
    pointer-events: auto;
    overflow: visible !important;
}

.sphereTopLeft {
    top: -15%;
    left: 15%;
}

.sphereTopRight {
    top: 10%;
    right: 2%;
}

.sphereBottomLeft {
    bottom: -15%;
    left: 2%;
}

/* Tablet and smaller screens */
@media (max-width: 1024px) {
    .sphereTopLeft,
    .sphereTopRight,
    .sphereBottomLeft {
        width: 700px;
        height: 700px;
    }
    
    .sphereTopLeft {
        top: -3%;
        left: 1%;
    }
    
    .sphereTopRight {
        top: 8%;
        right: 1%;
    }
    
    .sphereBottomLeft {
        bottom: -3%;
        left: 1%;
    }
}

/* Mobile screens */
@media (max-width: 768px) {
    .landingContainer {
        height: 100vh;
    }
    
    .sphereTopLeft,
    .sphereTopRight,
    .sphereBottomLeft {
        width: 450px;
        height: 450px;
    }
    
    .sphereTopLeft {
        top: -2%;
        left: 0%;
    }
    
    .sphereTopRight {
        top: 20%;
        right: 0%;
    }
    
    .sphereBottomLeft {
        bottom: -2%;
        left: 0%;
    }
}

/* Small mobile screens */
@media (max-width: 480px) {
    .sphereTopLeft,
    .sphereTopRight,
    .sphereBottomLeft {
        width: 280px;
        height: 280px;
    }
    
    .sphereTopLeft {
        top: -1%;
        left: -2%;
    }
    
    .sphereTopRight {
        top: 3%;
        right: -2%;
    }
    
    .sphereBottomLeft {
        bottom: -1%;
        left: -2%;
    }
}

/* Very large screens */
@media (min-width: 1920px) {
    .sphereTopLeft,
    .sphereTopRight,
    .sphereBottomLeft {
        width: 700px;
        height: 700px;
    }
    
    .sphereTopLeft {
        top: -5%;
        left: 2%;
    }
    
    .sphereTopRight {
        top: 10%;
        right: 2%;
    }
    
    .sphereBottomLeft {
        bottom: -5%;
        left: 2%;
    }
}